html {
  font-size: 100%;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--bg-light);
  font-family: var(--font-body);
  color: var(--light-text);
  overflow-x: hidden;
  box-sizing: border-box;
}

body a, a:link, a:visited, a:focus, a:hover, a:active, a:-webkit-any-link {
  color: inherit;
  text-decoration: none;
}

ul {
  list-style: none;
}

.uppercase {
  text-transform: uppercase;
}

:root {
  --primary: #DA3559;
  --primary-light: #AE2A47;
  --bg-dark: #333333;
  --bg-light: #FFFFFF;
  --dark-text: #333333;
  --light-text: #FFFFFF;
}

:root {
  --font-title: "Poppins", sans-serif;
  --font-body: "IBM Plex Sans", sans-serif;
}

h1, h2, h3, p {
  margin-top: 0;
}

nav {
  text-transform: uppercase;
}

.poppins {
  font-family: var(--font-title);
}

.container {
  display: flex;
  align-items: center;
  align-content: center;
  flex-direction: column;
}
.container--dark {
  background: var(--bg-dark);
}
.container--light {
  background: var(--bg-light);
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

main {
  max-width: 90rem;
  width: 100%;
  padding: 0.75rem 4rem 0;
}

.btn {
  box-sizing: border-box;
  max-width: 11.625rem;
  padding: 1.25rem 1.875rem;
  background: var(--primary);
  display: flex;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  font-size: 1.25rem;
  line-height: 0.625rem;
  font-family: var(--font-title);
}

.header {
  background-color: var(--bg-dark);
  color: var(--light-text);
  height: 6rem;
  max-width: 82.0625rem;
  width: 100%;
  display: flex;
  align-items: center;
}
.header__nav {
  height: 3.85625rem;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__nav__list {
  display: flex;
  align-items: center;
}
.header__nav__logo {
  background-image: url("./images/rakalogo.png");
  width: 9.8125rem;
  height: 100%;
  background-repeat: no-repeat;
  background-size: contain;
}
.header__nav__item {
  font-weight: 700;
  font-family: var(--font-title);
  font-size: 1.25rem;
  line-height: 16px;
}
.header__nav__item:not(:first-child) {
  margin-left: 3.25rem;
}

.hero {
  background-image: url("./images/hero-photo.png");
  background-repeat: no-repeat;
  height: 43.75rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.hero__text-container {
  grid-column: 1/1;
  align-self: center;
  justify-self: end;
  max-width: 34.375rem;
  margin-left: 25%;
}
.hero__text-container > * {
  margin: 0;
}
.hero__title {
  font-size: 4.0625rem;
  line-height: 4.6875rem;
  text-transform: uppercase;
}
.hero__subtitle {
  font-size: 2rem;
  line-height: 2.625rem;
  margin-bottom: 1.875rem;
}
.hero__btn {
  justify-self: start;
}

.statement {
  height: 29.4375rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.statement__copy {
  font-size: 2rem;
  line-height: 3.25rem;
  letter-spacing: 1%;
  text-align: center;
  max-width: 68.75rem;
}
.statement::after {
  content: "";
  width: 15.375rem;
  height: 0.25rem;
  background-color: var(--primary);
}